<template>
  <div class="Menu">

    <van-cell-group title="ÈÄâÈ°π Options">

      <van-cell center title="ÈöêËóèÊâæÂà∞ÁöÑ" label="Hide Found" value="">
        <van-switch v-model="hideFound"/>
      </van-cell>

      <van-cell center title="ÈîÆÁõò" label="Keyboard" value="">
        <van-switch v-model="keyboard" @input="inputKeyboard"/>
      </van-cell>

      <van-cell center title="Ë°åÂºè" label="Line" value="">
        <van-switch v-model="line" @input="inputLine"/>
      </van-cell>

      <van-cell center title="ÈáçÂ§ç" label="Repeat" value="">
        <van-switch v-model="repeat"/>
      </van-cell>

      <van-cell center title="Ë°åÂàóÊï∞" label="Row and Column" value="">
        <van-row>
          <van-col span="12">
            <van-stepper v-model="row" :min="1" :button-size="bottonSize"/>
          </van-col>
          <van-col span="12">
            <van-stepper v-model="col" :min="1" :button-size="bottonSize"/>
          </van-col>
        </van-row>
      </van-cell>

      <van-cell center title="Êï∞Â≠óËåÉÂõ¥" label="Number Range" value="">
        <van-row>
          <van-col span="12">
            <van-stepper v-model="a1" :min="0" :max="a2" :button-size="bottonSize"/>
          </van-col>
          <van-col span="12">
            <van-stepper v-model="a2" :min="a1" :max="keyboard ? 9: undefined" :button-size="bottonSize"/>
          </van-col>
        </van-row>
      </van-cell>

      <van-cell center title="Ê†πÊçÆË°åÂàóÊï∞ËÆ°ÁÆóÊï∞Â≠óËåÉÂõ¥" label="Calculate Number Range by rows and columns" @click="range()"/>

    </van-cell-group>

    <van-divider :style="{ borderColor: '#1989fa' }"/>

    <van-cell-group title="ÂºÄÂßã Start">

      <!-- Áî® 0 ÂºÄÂßãÊ†áËÆ∞ÊòØÊï∞Â≠ó -->
      <van-cell @click="memory('0' + a1,a2 - a1)" title="Êï∞Â≠óËÆ∞ÂøÜ" label="Number Memory"/>

      <van-cell @click="memory('A', 26)" title="Â§ßÂÜôÂ≠óÊØçËÆ∞ÂøÜ" label="Upper Case Letters Memory"/>
      <van-cell @click="memory('a', 26)" title="Â∞èÂÜôÂ≠óÊØçËÆ∞ÂøÜ" label="Lower Case Letters Memory"/>

      <!-- https://www.unicode.org/charts/charindex.html#J-->
      <van-cell @click="memory('„ÅÅ', 86)" title="Âπ≥ÂÅáÂêçËÆ∞ÂøÜ" label="Japanese Hiragana Memory"/>
      <van-cell @click="memory('„Ç°', 95)" title="ÁâáÂÅáÂêçËÆ∞ÂøÜ" label="Japanese Katakana Memory"/>

      <!-- https://www.emojiall.com/ -->
      <!-- https://www.qqxiuzi.cn/zh/emoji.html -->
      <van-cell @click="memory('üê≠', 17)" title="Âä®Áâ©Ë°®ÊÉÖËÆ∞ÂøÜ" label="Animal Emoji Memory"/>
      <van-cell @click="memory('üé†', 52)" title="Á∫¶‰ºöË°®ÊÉÖËÆ∞ÂøÜ" label="Dating Event Emoji Memory"/>

    </van-cell-group>

  </div>
</template>

<script>
export default {
  data() {
    return {
      bottonSize: '20px',
      hideFound: true,
      line: false,
      repeat: false,
      keyboard: false,
      a1: 0,
      a2: 25,
      row: 5,
      col: 5,
    }
  },

  methods: {

    memory(symbol, keyLen) {
      this.$router.replace('/Memory' +
          '?row=' + this.row +
          '&col=' + this.col +
          '&symbol=' + symbol +
          '&keyLen=' + keyLen +
          '&hideFound=' + this.hideFound +
          '&line=' + this.line +
          '&repeat=' + this.repeat +
          '&keyboard=' + this.keyboard);
    },

    inputKeyboard(checked) {
      if (checked) {
        this.repeat = true;
      } else {
        this.range()
        this.repeat = false;
      }
      this.keyboard = checked;
    },

    inputLine(checked) {
      if (checked) {
        this.a1 = 0;
        this.a2 = 9;
        this.row = 4;
        this.repeat = true;
      } else {
        this.row = 5;
        this.range()
        this.repeat = false;
      }
      this.line = checked;
    },

    range() {
      this.a2 = this.a1 + this.row * this.col;
    },
  },
};
</script>